import Endpoint from '@/services/Endpoint'

export default {
  geos (payload) {
    return Endpoint().get('/s1/commons/basic/geo', {
      params: payload
    })
  },
  geoAssocs (payload) {
    return Endpoint().get('/s1/commons/basic/geo/assoc', {
      params: payload
    })
  },
  createGeo (payload) {
    return Endpoint().post('/s1/commons/basic/geo', payload)
  },
  modifyGeo (payload) {
    return Endpoint().patch('/s1/commons/basic/geo', payload)
  }
}
